.box1 {
    
    background: white;
    width:30%;
    border-radius:8px;
    font-size: 21px;
    border:1px solid rgb(231, 231, 231);
    box-shadow: 3px 3px 5px rgb(248, 248, 248);
    /* width: fit-content; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding:10px 10px 10px 10px;
    
}
input{
    border:none;
    width:90%;
    outline:none;
    font-size: 18px;
 
}



img{
    width:200px;
    height:140px;
}

.box2{
    margin-top: 24px;
    width:50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content:space-evenly;
    
}

.temp{
    font-size:30px;
    font-weight: 600;
}

.location{
    color:silver;
    margin-top:4px;
}

span{
    width:5%;
    font-size: 21px;
    color:silver;
   
}

span i{
    float: right;
}

.fa-magnifying-glass:active{
    cursor:pointer;
    transform:scale(0.8);
    transition:1s ease;
}

@media screen and (max-width:1200px)
{
    .box1 {
       
        width:60%;
        font-size: 21px;
        justify-content:space-evenly;
        
    }
   
    
    
    img{
        width:200px;
        height:140px;
    }
    
    .box2{

        width:60%;
        justify-content:space-around;
        
    }

   
    
    
   
    
    
}


@media screen and (max-width:450px)
{
    .box1 {
       
        width:90%;
        font-size: 19px;
        justify-content:space-evenly;
        
    }
   
    
    img{
        width:150px;
        height:100px;
    }
    
    .box2{
        font-size: 19px;
        width:90%;
        gap:10px;
        justify-content:space-around;
        flex-wrap: wrap;
    }

    span{
        font-size: 19px;
    }
    
    
    
}

