.cloud{
    margin-top:4px;
    background: white;
    border-radius:8px;
    
    width:454px;
    
    height:fit-content;
    box-shadow: rgba(219, 220, 222, 0.2) 0px 7px 23px;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}

.divd{
    padding:12px 0px;
 width:50%;
position:relative;


}

.divd1{
    width:50%;
}

.valued{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-70%,-50%);
    font-size: 18px;
    color:black;
}


.gust{
    color:silver;
}

.gust1{
    margin-top: 110px;
    color:silver;
}

