.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.prog_container{
     background-color: rgb(252, 252, 252);
}

.container1{
  margin-top: 24px;
  padding-top: 1.6vw;
  width:100%;
  display: flex;
  flex-direction: row;
  justify-content:space-evenly;
  flex-wrap: wrap;
  background-color: rgb(252, 252, 252);
  padding-bottom:1.6vw;
}

.left{

  width:65vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap:10px;
  /* background-color: #282c34; */
  justify-content: center;
  margin-top:-5px;
}
.right{
  width:32vw;
  margin-right:15px;
  /* background-color: #282c34; */
}

body{
  overflow-x: hidden;
  /* background-color: rgb(252, 252, 252); */
}

@media (max-width:780px) and (min-width:600px)
{

  .left{

    width:55vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap:10px;
    /* background-color: #282c34; */
    justify-content: center;
    margin-top:-5px;
  }
  .right{
    width:42vw;
    margin-right:15px;
    /* background-color: #282c34; */
  }

}


@media (max-width:680px) and (min-width:500px)
{

  .left{

    width:45vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap:10px;
    /* background-color: #282c34; */
    justify-content: center;
    margin-top:-5px;
  }
  .right{
    width:45vw;
    margin-right:15px;
    /* background-color: #282c34; */
  }

}

@media (max-width:500px) and (min-width:200px){
  .left{
    margin: auto;
    width:95vw;
    gap:14px;

    
  }
  .right{
    width: 95vw;
    margin: auto;
  }

  .container1{
   
    flex-direction: column;
    padding-top: 2.5vw;
   
  }

}

