.smcomp{
    margin-top:4px;
    background: white;
    border-radius:8px;
    padding:24px 12px;
    width:200px;
    
    height:fit-content;
    box-shadow: rgba(219, 220, 222, 0.2) 0px 7px 23px;
}

.d1{
    text-align: left;
}

.smcomp .div{
    margin-top:14px;
   
}

.smcomp i{
    font-size: 37px;
}

.d3{
    text-align: left;
    margin-left:4px;
}

@media (max-width:1063px) and (min-width:900px)
{
    .smcomp{
        
        width:260px;
    }
    
    .smcomp i{
        font-size: 35px;
    }
    
    .d3{
        text-align: left;
        margin-left:8px;
    }

}

@media (min-width:780px) and (max-width:900px)
{
    .smcomp{
        
        width:220px;
    }
    
    .smcomp i{
        font-size: 35px;
    }
    
    .d3{
        text-align: left;
        margin-left:8px;
    }

}

@media (max-width:780px) and (min-width:680px)
{
    .smcomp{
        
        width:150px;
    }
    
    .smcomp i{
        font-size: 28px;
    }
    
    .d3{
        text-align: left;
        margin-left:8px;
    }

}

@media (max-width:680px) and (min-width:500px){
    .smcomp{
        
        width:350px;
    }
}

@media (max-width:500px) and (min-width:200px){

    .smcomp{
        margin-top:4px;
        background: white;
        border-radius:8px;
        padding:4px 2px;
        width:43vw;
        font-size: 14px;
    }
    
    .d1{
        text-align: left;
        margin-left:4px;
    }
    
    .smcomp .div{
        margin-top:14px;
       
    }
    
    .smcomp i{
        font-size: 24px;
    }
    .d3{
        margin-left:4px;
    }
    
    
}

    
    